import myTabs from '@/components/myTabs/myDeviceMore.vue'
import myPull from '@/static/js/myDeviceMore.js'
import faIcon from "@/components/fa-icon/fa-icon.vue"
import request from '../../api/request.js'
import global from '../../static/js/global.js'
export default {
	components:{myTabs,myPull,faIcon},
	data() {
		return {
			userList: [],
			/* 显示遮罩 */
			showShade: false,
			/* 窗口尺寸 */
			winSize: {},
			page:1,
			devId:'',
			curType:'',
			total:''
		}
	},
	onLoad(e) {
		this.devId=e.id
		this.getWarnList()
	






	},
	methods: {
		/**
		 * @name 改变tab
		 * @param val 索引
		 */
		tapChange(val){
			if(this.curType!=val){
				this.curType=val
				this.initIndex=val
				this.page=1
				this.total=''
				this.userList=[]
				if(val==0){
					this.getWarnList()
				}else if(val==1){
					this.getRepairList()
				}
			}
		},
		/* 获取列表数据 */
		getWarnList() {
			var that = this
			var param = {
				openId:'wx123456789',
				devId:this.devId,
				page:this.page,
				limit:10
			}
			request.apiGet('/toc/deviceWarn/listWarnByDev/',param).then((res) =>{
				if(res.code == '0'){
					this.userList=this.userList.concat(res.data)
					this.total=res.total
				}
			})
		},
		getRepairList() {
			var that = this
			var param = {
				openId:'wx123456789',
				devId:this.devId,
				page:this.page,
				limit:10
			}
			request.apiGet('/toc/deviceRepair/listRepairByDev/',param).then((res) =>{
				if(res.code == '0'){
					this.userList=this.userList.concat(res.data)
					this.total=res.total
				}
			})
		},
		scrolltolower(){
			if(this.userList.length<this.total){
				this.page++
				if(this.curType==0){
					this.getWarnList()
				}else if(this.curType==1){
					this.getRepairList()
				}
			}
		}
	},
	mixins:[myPull({})],
	
}
